@import "abstracts/variable.sass";
@import "abstracts/mixins.sass";
@import "abstracts/functions.sass";

@import "base/animation.sass";


@font-face {
    font-display: swap;
    font-family: "_Lato";
    src: url('../fonts/Lato-Regular.ttf');
    font-weight: normal;
    font-style: normal; }

@font-face {
    font-display: swap;
    font-family: "_Lato";
    src: url('../fonts/Lato-Bold.ttf');
    font-weight: bold;
    font-style: normal; }

* {
    box-sizing: border-box; }

html {
    font-size: 62.5%; }

body {
    font-family: "_Lato", sans-serif;
    font-weight: 400;
    color: #777777;
    font-size: 1.6rem;
    letter-spacing: 0.2rem;
    padding: 5rem;
    background: $color-white; }

.container {
    background: #F7F7F7;
    padding: 0; }

.header {
    position: relative;
    height: 95vh;
    background: linear-gradient(90deg, rgba($color-brown-light, 0.7) 0.03%, rgba($color-black, 0.7) 99.94%), url("../img/pexels-mehmet-turgut-kirkgoz-5865634 2.jpg") center / cover no-repeat;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%);
    &__logo {
        position: absolute;
        top: 5rem;
        left: 5rem;
        height: 7rem;
        width: 5.6rem; }
    &__box {
        position: absolute;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        text-align: center; } }

.title-first {
    color: $color-white;
    animation-name: leftToRight;
    animation-duration: 4s;
    animation-delay: 1.5s;
    margin-bottom: 6.5rem;
    &__main {
        display: block;
        font-weight: 800;
        font-size: 5rem;
        line-height: 6rem;
        letter-spacing: 1rem;
        margin-bottom: 1rem; }
    &__second {
        display: block;
        font-weight: 100;
        font-size: 4rem;
        line-height: 4.8rem;
        letter-spacing: 0.5rem; } }
